import { defineComponent, ref } from '@nuxtjs/composition-api';
export default defineComponent({
    props: {
        card: {
            type: Object,
            required: true
        }
    },
    setup(props, { emit }) {
        const item = ref();
        const newChecklistItem = ref('');
        item.value = props.card;
        const onChange = (key, value) => {
            emit(`change`, item.value, { [key]: value });
        };
        const onAddChecklistItem = () => {
            item.value.checklists.push({
                done: false,
                name: newChecklistItem.value
            });
            newChecklistItem.value = '';
        };
        return {
            item,
            onChange,
            onAddChecklistItem,
            newChecklistItem
        };
    }
});
